.blogPage {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    width: inherit;
    margin: auto;
}

.blogHeaderWrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-height: fit-content;
    padding: 0.5rem 0;
}

.blogHeader {
    font-weight: 400;
    font-size: 16px;
    line-height: 52px;
}

.blogHeaderIcons {
    display: flex;
    gap: 2rem;
}

.greyIcon {
    color: #8D8989;
}

.blogArticleWindow {
    display: flex;
    justify-content: space-evenly;
}


.featuredPostText {
    font-weight: 400;
    font-size: 30px;
}

.featuredPostBox {
    background-color: #8DB7E3;
    border-radius: 20px;
    height: fit-content;
    width: 550px;
}

.featuredPostImgWrap {
    width: inherit;
    max-height: 230px;
    border-radius: 20px 20px 0 0;
}

.FeaturedPostImg {
    width: inherit;
    max-height: inherit;
    border-radius: inherit;
}

.featuredPostTopic {
    margin: 14px 25px;
    font-weight: 500;
    font-size: 18px;
    line-height: 39px;
}

.featuredPostInfo {
    display: flex;
    gap: 50px;
    margin: 0 25px 14px 25px;
}

.featuredPostDate {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
}

.featuredPostLength {
    font-weight: 300;
    font-size: 14px;
    line-height: 17px;
    display: flex;
    align-items: center;
    gap: 0.5rem;
}


.featuredPostSynopsis {
    display: flex;
    max-width: 600px;
    flex-wrap: wrap;
    font-weight: 300;
    font-size: 20px;
    line-height: 25px;
    margin: 0 25px;
}

.featuredPostProfile {
    margin: 13px 0 0 25px;
    padding-bottom: 17px;
    display: flex;
    align-items: center;
    gap: 30px;
}

.recommendedPostWrap {
}

.recommendedPost {
    font-weight: 400;
    font-size: 30px;
    line-height: 35px;
}

.RecommendedPostWindowBox {
    display: flex;
    flex-direction: column;
    gap: 40px;
}

.latestPostWrap {
    padding-top: 3rem;
    margin: auto;
    max-width: 90vw;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding-bottom: 2rem;
}

.latestPostHeader {
    font-weight: 400;
    font-size: 30px;
    line-height: 37px;
    margin-left: 80px;
}

.latestPostWindowBox {
    display: flex;
    max-width: inherit;
    flex-wrap: wrap;
    gap: 50px;
}


@media only screen and (max-width: 1280px) {
}

@media only screen and (max-width: 850px) {
    .blogArticleWindow {
        flex-direction: column;
        margin: 0 auto;
        gap: 1rem;
    }


    .RecommendedPostWindowBox {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
    }
}

@media only screen and (max-width: 700px) {
    .blogHeader {
        font-size: 12px;
    }

    .blogIcon {
        height: 10px;
        padding-bottom: 1rem;
    }

    .blogArticleWindow {
        margin: auto 1.5rem;
    }

    .featuredPostBox {
        width: 70vw;
        margin: auto;
    }

    .featuredPostTopic {
        font-weight: 300;
        font-size: 15px;
        line-height: 30px;
    }

    .featuredPostInfo {
        display: flex;
        gap: 0;
        margin: 0 0 14px 0;
        align-items: center;
        justify-content: space-around;
    }

    .featuredPostDate{
        font-weight: 200;
        font-size: 13px;
        line-height: 15px;
    }

    .featuredPostLength {
        font-weight: 200;
        font-size: 13px;
        line-height: 15px;
    }

    .featuredPostSynopsis {
        font-weight: 200;
        font-size: 15px;
        line-height: 20px;
    }

    .blogMobileText {
        font-size: 23px;
        padding: 1rem 0 0 2rem;
    }

    .RecommendedPostWindowBox {
        display: flex;
    }

    .recommendedPost {
        font-weight: 400;
        font-size: 23px;
        line-height: 30px;
    }

    .featuredPostImgWrap {
        width: inherit;
    }

    .AuthorImg {
        height: 35px;
    }

    .featuredPostAuthorName {
        font-size: 15px;
        font-weight: 200px;
    }

    .latestPostWrap {
        padding-top: 1rem;
    }

    .latestPostHeader {
        margin-left: 20px;
    }

}